<template>
  <div>
    <b-row class="mt-3">
      <b-col md="12">
        <b-card
          header="訂單明細"
          header-text-variant="dark"
          header-tag="header"
          header-bg-variant="ivory"
        >
          <b-table
            striped
            hover
            stacked="lg"
            ref="selectableTable"
            :fields="fields"
            :items="items"
          >
            <!-- Optional default data cell scoped slot -->
            <template v-slot:cell()="data">
              <span v-text="data.value"></span>
            </template>
            <!-- A custom formatted column -->
            <template v-slot:cell(name)="data">
              <router-link
                class="text-decoration-none text-primary"
                :to="'/product/' + data.item.isbn"
              >
                <span v-text="data.value"></span>
              </router-link>
            </template>
            <!-- A custom formatted column -->
            <template v-slot:cell(salePrice)="data">
              <span v-text="salePrice(data)"></span>
            </template>
            <!-- A custom formatted column -->
            <template v-slot:cell(subtotal)="data">
              <span v-text="calSubTotal(data)"></span>
            </template>
          </b-table>
          <div class="d-flex bg-light">
            <div class="ml-auto">
              <p class="mt-3 mr-3">
                共
                <span class="text-danger">{{ getItemsLength }}</span>
                項商品，數量
                <span class="text-danger" v-text="totalQuantity()"></span>
                個，總金額 NT$
                <span class="text-danger" v-text="totalAmount()"></span> 元
              </p>
            </div>
          </div>
        </b-card>
      </b-col>
    </b-row>

    <b-row class="mt-3" v-if="isOrderNotEmpty">
      <b-col md="12">
        <b-card
          header="付款方式及寄送資訊"
          header-text-variant="dark"
          header-tag="header"
          header-bg-variant="ivory"
        >
          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="orderNo">訂單編號</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="orderNo">{{ getOrderNo }}</span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="orderNo">訂單時間</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="orderNo">{{ getOrderDate }}</span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="orderNo">訂單狀態</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="orderNo">{{ getOrderStatus }}</span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="deliver">配送方式</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="deliver">{{ getDeliver }}</span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="payment">付款方式</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="payment">{{ getPayment }}</span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="invoiceType">發票資訊</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="invoiceType">{{ getInvoiceType }}</span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="invoiceType">發票號碼</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="invoiceType">{{ getInvoiceNumber }}</span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="buyerName">訂購人</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="buyerName" v-text="getBuyer"></span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="receiverName">收件人</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="receiverName" v-text="getReceiverName"></span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="receiverContactNumber">收件人聯絡電話</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="receiverContactNumber">{{ getContactNumber }}</span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="receiverMobile">收件人手機號碼</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="receiverMobile">{{ getMobile }}</span>
            </b-col>
          </b-row>

          <b-row class>
            <b-col md="2" class="d-flex align-items-center ml-1">
              <label for="receiverAddress">收件人地址</label>
            </b-col>
            <b-col md="9" class="ml-1">
              <span id="receiverAddress">{{ getAddress }}</span>
            </b-col>
          </b-row>
        </b-card>
      </b-col>
    </b-row>
  </div>
</template>
 
<script>
import { mapGetters } from "vuex";
// import buyer from "@/api/buyer.js";
import config from "@/common/config";
import styles from "@/assets/css/progress-bar.css";
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faAngleDoubleRight,
  faAngleDoubleLeft,
} from "@fortawesome/free-solid-svg-icons";
import ApiService from "@/common/api.service";
import util from "@/common/util";
import { JWT_EXPIRED_MSG } from "@/common/constant";

library.add(faAngleDoubleRight, faAngleDoubleLeft);

export default {
  name: "Order",
  data() {
    return {
      // https://bootstrap-vue.org/docs/components/table#row-select-support
      // selectMode: "multi",
      // onRowSelected(items) {
      //   this.selected = items
      // },
      fields: config.orderFields,
      // buyer: null,
      // orderNo: "20201130092604879QTP",
      // orderDate: "2020-11-30 09:26:05",
      // orderAmount: 2127,
      // orderItems: [],
      items: [],
      order: {},
    };
  },
  components: {},
  computed: {
    // items() {
    //   return this.$store.getters["cart/cartProducts"];
    // },
    isOrderNotEmpty() {
      return !this.isEmpty(this.order);
    },
    getItemsLength() {
      return this.items.length;
    },
    getOrderNo() {
      return this.order.orderNo;
    },
    getOrderDate() {
      return this.order.orderDate;
    },
    getOrderStatus() {
      let orderStatus;
      switch (this.order.orderStatus) {
        case "PENDING":
          orderStatus = "待處理";
          break;
        case "PROCESSING":
          orderStatus = "處理中";
          break;
        case "SHIPPED":
          orderStatus = "運送中";
          break;
        case "RETURNED":
          orderStatus = "已退貨";
          break;
        case "CANCELED":
          orderStatus = "已取消";
          break;
        default:
          orderStatus = "未知";
          break;
      }
      return orderStatus;
    },
    getDeliver() {
      return this.order.deliver === "homeDelivery" ? "宅配" : "郵寄";
    },
    getPayment() {
      return this.order.payment === "COD" ? "貨到付款" : "";
    },
    getInvoiceType() {
      return this.order.invoiceType === "paper" ? "紙本發票" : "";
    },
    getInvoiceNumber() {
      return this.order.invoiceNumber;
    },
    getBuyer() {
      return this.order.buyer;
    },
    getReceiverName() {
      return this.order.receiverName;
    },
    getContactNumber() {
      console.log(
        "this.order.receiverExt.length : ",
        this.order.receiverExt.length
      );
      let length = this.order.receiverExt.length;
      let ext = length == 0 ? "" : " 分機 " + this.order.receiverExt;
      return this.order.receiverArea + "-" + this.order.receiverPhone + ext;
    },
    getMobile() {
      return this.order.receiverMobile;
    },
    getAddress() {
      return (
        this.order.zipcode +
        this.order.county +
        this.order.district +
        this.order.receiverAddress
      );
    },
    getQRCode() {
      return this.QRCode;
    },
  },
  methods: {
    isEmpty(obj) {
      for (var key in obj) {
        if (obj.hasOwnProperty(key)) return false;
      }
      return true;
    },
    salePrice(data) {
      data.item.salePrice = this.calSalePrice(
        data.item.listPrice,
        data.item.discount
      );
      return data.item.salePrice;
    },
    calSalePrice(price, discount) {
      return Math.round((price * discount) / 100);
    },
    calSubTotal(data) {
      return data.item.salePrice * data.item.quantity;
    },
    totalQuantity() {
      //https://fred-zone.blogspot.com/2017/01/javascript-mapreduce.html
      //https://developer.mozilla.org/zh-TW/docs/Web/JavaScript/Reference/Global_Objects/Array/map
      //https://developer.mozilla.org/zh-TW/docs/Web/JavaScript/Reference/Global_Objects/Array/Reduce
      return this.items
        .map((item) => parseInt(item.quantity, 10))
        .reduce((acc, cur) => acc + cur, 0);
    },
    totalAmount() {
      return this.items
        .map(
          (item) =>
            this.calSalePrice(item.listPrice, item.discount) *
            parseInt(item.quantity, 10)
        )
        .reduce((acc, cur) => acc + cur, 0);
    },
  },
  mounted() {},
  created() {
    ApiService.setHeader();
    ApiService.get("order", this.$route.params.orderNo)
      .then((response) => {
        console.log("Order.vue created response : " + JSON.stringify(response));
        ApiService.deleteHeader();
        this.items = JSON.parse(JSON.stringify(response.data.items));
        this.order = JSON.parse(JSON.stringify(response.data.order));
      })
      .catch((err) => {
        console.log("err : " + err);
        console.log("err JSON.stringify : " + JSON.stringify(err));
        console.log("err.response : " + JSON.stringify(err.response));
        // let msg = "";
        if (err.response.data.message) {
          console.log(
            "err.response.data.message : " + err.response.data.message
          );
          ApiService.deleteHeader();

          let msg = util.checkJWTExpired(err, this);
          // if (util.testJWTExpiredMsg(err.response.data.message)) {
          //   msg = JWT_EXPIRED_MSG;
          //   this.$store.dispatch("auth/purgeAuth");
          //   console.log("this.$route.path", this.$route.path);
          //   // https://router.vuejs.org/zh/guide/essentials/navigation.html
          //   this.$router.push({
          //     // path: "login",  // Uncaught (in promise) Error: Redirected when going from "/order/20201201215051645NNV" to "/order/login"
          //     path: "/login",
          //     query: { redirect: this.$route.path },
          //   });
          // } else {
          //   msg = err.response.data.message;
          // }
          this.$store.dispatch("modal/setModalMsg", msg);
          this.$store.dispatch("modal/setModalShow", true);
        }
      });
  },
};
</script>
<style>
/* https://www.w3schools.com/colors/colors_hex.asp */
.bg-AliceBlue {
  background-color: AliceBlue !important;
}
.bg-ivory {
  background-color: ivory !important;
}
</style>